import React, { Component } from 'react';
import MentorSelect from './components/MentorSelect';
import './App.css';
import data from './main.json';

const { tasks, mentors } = data;

const tasksObj = {};
tasks.forEach(task => {
  tasksObj[Object.keys(task)] = task[Object.keys(task)];
});

class App extends Component {
  state = {
    mentor: 'aliaksandrzahorski'
  };

  handleInputChange = e => {
    this.setState({ mentor: e.value });
  };

  addColor = (taskName, student) => {
    const studentData = mentors[this.state.mentor][student];
    if (studentData[taskName] !== undefined) return 'lightgreen';
    switch (tasksObj[taskName]) {
      case 'In Progress':
        return 'yellow';
      case 'Checked':
        return 'crimson';
      case 'ToDo':
        return 'lightgrey';
      case 'Checking':
        return 'pink';
      default:
        return 'pink';
    }
  };

  render() {
    const { mentor } = this.state;
    const students = Object.keys(mentors[mentor]);

    return (
      <div className="App">
        <h1>Mentor Dashboard</h1>
        <div className="select-container">
          <MentorSelect
            options={Object.keys(mentors).map(mentor => ({ value: mentor, label: mentor }))}
            default={{ value: this.state.mentor, label: this.state.mentor }}
            handleInputChange={this.handleInputChange}
          />
        </div>
        <h2>{this.state.mentor}</h2>
        <table border="0">
          <thead>
            <tr>
              <th>Tasks \ Students</th>
              {students.map(student => (
                <th key={student}>{student}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {tasks.map(task => {
              const taskName = Object.keys(task)[0];
              return (
                <tr key={taskName}>
                  <td>{taskName}</td>
                  {students.map(student => (
                    <td
                      style={{ backgroundColor: this.addColor(taskName, student) }}
                      key={taskName + student}
                    >
                      {mentors[mentor][student][taskName]}
                    </td>
                  ))}
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    );
  }
}

export default App;
